/* Showcase */

.showcase-horizontal{
  position: fixed;
  -webkit-transform: rotate(-90deg);
  letter-spacing: 0.06em;
  font-size: 8em;
  font-weight: 900;
  margin: 0;
  padding: 0;
  left: -45%;
  width: 100vw;
}

.wrapper {
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-auto-rows: 1fr 1fr;
  padding-top: 5%;
}

/* Best of */
.wrapper--box1 {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  position: relative;
  
}

.wrapper--box1--img, .wrapper--box3--img, .wrapper--box5--img, .wrapper--box7--img{
  width: 22vw;
  -webkit-box-shadow: 13px 19px 21px -8px rgba(79, 70, 79, 0.96);
  -moz-box-shadow: 13px 19px 21px -8px rgba(79, 70, 79, 0.96);
  box-shadow: 13px 19px 21px -8px rgba(79, 70, 79, 0.96);
}

.wrapper--box1--title, .wrapper--box3--title, .wrapper--box5--title, .wrapper--box7--title{
  display: block;
  position: absolute;
  left: -40%;
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transform: rotate(-90deg) translateX(-10%);
  font-family: "Abril Fatface", cursive;
  letter-spacing: 0.06em;
  font-size: 5em;
  font-weight: 900;
  z-index: 1;
}

.wrapper--box1--title{
  left: -90%;
  top: 20%;
  font-size: 4em;
}

.wrapper--text1--line {
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 1;
}

.wrapper--text1--line::before,.wrapper--text2--line::before, .wrapper--text3--line::before, .wrapper--text4--line::before {
  content: "";
  display: inline-block;
  background: black;
  height: 1px;
  width: 15vh;
  margin-top: 65%;
  margin-left: -7%;
}

.wrapper--box2--text {
  grid-column-start: 6;
  grid-column-end: 8;
  grid-row-start: 1;
  width: 100%;
}

/* De Stijl */
.wrapper--box3 {
  grid-column-start: 7;
  grid-column-end: 9;
  grid-row-start: 2;
  position: relative;
}

.wrapper--box3--title {
  left: -45%;
}

.wrapper--text2--line {
  grid-column-start: 6;
  grid-column-end: 7;
  grid-row-start: 2;
}

.wrapper--text2--line::before {
  margin-top: 65%;
  margin-left: 8%;
}

.wrapper--box4--text {
  grid-column-start: 4;
  grid-column-end: 6;
  grid-row-start: 2;
  width: 100%;
}


/* Showcase page2 */
.wrapper--box5--title, .wrapper--box7--title {
  -webkit-transform: rotate(90deg) translateY(20%);
}
/* Elephant */
.wrapper--box5 {
  grid-column-start: 4;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 2;
  position: relative;
  margin-top: 5%;
  // margin-left: -15%;
}

.wrapper--box5--title{
  left: 46%;
}

.wrapper--text3--line {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
}

.wrapper--text3--line::before {
  margin-top: 120%;
  margin-left: 10%;
}

.wrapper--box6--text {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  width: 100%;
  margin-top: 10%;
}

/* White Blood Cells */
.wrapper--box7 {
  grid-column-start: 6;
  grid-column-end: 8;
  grid-row-start: 2;
  position: relative;
}

.wrapper--box7--title {
  font-size: 4em;
  left: 26%;
  width: 37vw;
}

.wrapper--text4--line {
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 2;
}

.wrapper--text4--line::before {
  margin-top: 65%;
  margin-left: 8%;
}

.wrapper--box8--text {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 2;
  width: 100%;
}

/* Showcase Animation */
.animation-img{
  opacity: 0;
  transform: translateY(-50px);
  transition: all 1.2s ease-out;
}

.fadeInImg{
  opacity: 1;
  transform: translateY(0);
}

.showcase-horizontal .letter{
  display: inline-block;
  line-height: 1em;
}

.animation-text{
  opacity: 0;
  transform: translateY(50px);
  transition: all 1.2s ease-out;
}

.fadeOutText{
  opacity: 1;
  transform: translateY(0);
}
